































































.number-markup__input {
  display: flex;
  justify-content: center;
  flex-flow: wrap row;
  align-items: center;
  .label {
    font-size: inherit;
  }
  .input-field {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    padding-left: 42px;
    &__input {
      width: 131px;
      height: 50px;
      text-align: center;
    }
    &__unit {
      margin: 0 8px;
    }
  }
}
